.friendMessage {
    font-family: Montserrat, sans-serif;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
    margin-bottom: 5px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
}

.friendText {
    margin-left: 15px;
    padding: 7px 13px;
    background: #0066CC;
    color: #FFF;
    font-size: 1rem;
    font-weight: 400;
    border-radius: 10px;
    position: relative;
    box-shadow:  2px 2px 2px rgba(29, 33, 38, 0.10);
}

.friendText:after {
    position: absolute;
    bottom: 0;
    left: -15px;
    content: '';
    width: 0;
    height: 0;
    border-bottom: 20px solid #0066CC;
    border-left: 17px solid transparent;
    border-right: 25px solid transparent;
}

.friendName {
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 3px;
}

.friendMessageText {
    font-family: inherit;
}

.friendTime {
    position: relative;
    left: 9px;
    font-size: 0.625rem;
    font-weight: 600;
}

