*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box
}

.App {
    --header_height: 60px;
}

.container {
    margin: 0 auto;
    width: 1140px;
}

.hw {
    border-top: 1px solid #D9D9D9;
    border-bottom: 1px solid #D9D9D9;
    padding: 24px 0;
}

.hwTitle {
    font-family: 'Montserrat', sans-serif;
    padding: var(--header_height) 0 10px;
    font-weight: 600;
    font-size: 1.375rem;
}