.sendForm {
    display: flex;
    gap: 44px;
    margin-top: 80px;
    font-family: Montserrat, sans-serif;
}

.textarea {
    flex-grow: 2;
    border-radius: 20px;
    padding: 10px 24px;
    resize: none;
    outline: none;
    background: #F5F7FB;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    border: none;
    color: rgba(0, 0, 0, 0.60);
    font-size: 14px;
    font-family: inherit;
    font-weight: 400;
}

.button {
    font-family: inherit;
    border-radius: 20px;
    padding: 10px 38px;
    border: none;
    background: #06C;
    color: #fff;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
}

.button:hover {
    background: #0066CCaa;
}

.button:active {
    background: #0066CCaa;
}

.textarea:focus {
    border: 1px solid #ccc;
}
