.message {
    font-family: Montserrat, sans-serif;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    margin-bottom: 5px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
}

.text {
    margin-right: 15px;
    padding: 7px 13px;
    background: #0066CC;
    color: #FFF;
    font-size: 1rem;
    font-weight: 400;
    border-radius: 10px;
    position: relative;
    box-shadow:  -2px 2px 2px rgba(29, 33, 38, 0.10);
}

.text:after {
    position: absolute;
    bottom: 0;
    right: -15px;
    content: '';
    width: 0;
    height: 0;
    border-bottom: 20px solid #0066CC;
    border-left: 25px solid transparent;
    border-right: 17px solid transparent;
}

.name {
    text-align: end;
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 3px;
}

.messageText {
    font-family: inherit;
}

.time {
    text-align: end;
    position: relative;
    right: 9px;
    font-size: 0.625rem;
    font-weight: 600;
}