.greetingForm {
}

.inputAndButtonContainer {
    max-width: 460px;
    display: flex;
    gap: 12px
}

.error {
    color: #cc0000;
}

.input {
    border: 1px solid #D1D1D1;
    outline: none;
    border-radius: 3px;
    padding: 8px 24px;
}

.input:focus {
    border: 1px solid #333;
}

.errorInput {
    border: 1px solid #cc0000;
    outline: none;
    border-radius: 3px;
    padding: 8px 24px;
    background-color: rgba(255 0 0 / .2);
}

.button {
    align-self: flex-start;
    padding: 8px 24px;
    background-color: #0066CC;
    color: #fff;
    border-radius: 3px;
    border: none;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    cursor: pointer;
}

.button:focus {
    background-color: #0066CC99;
}

.button:active {
    background-color: #0066CC99;
}

.button:disabled {
    background-color: #ccc;
}

.text {
    color: rgba(0 0 0 / .5);
    font-size: 0.875rem;
    font-weight: 400;
    margin-bottom: 10px;
}

.greeting {
    font-family: Montserrat, sans-serif;
    font-size: 2rem;
    margin-top: 10px;
}
